		//监听image加载完成
		onImageLoad(key, index) {
			this.$set(this[key][index], 'loaded', 'loaded');
		},
		//监听image加载失败
		onImageError(key, index) {
			this[key][index].image = '/static/errorImage.jpg';
		},
		navToLogin(){
			uni.navigateTo({
				url: '/pages/public/login'
			})
		},
		 //选中状态处理
		check(type, index){
			if(type === 'item'){
				this.cartList[index].checked = !this.cartList[index].checked;
			}else{
				const checked = !this.allChecked
				const list = this.cartList;
				list.forEach(item=>{
					item.checked = checked;
				})
				this.allChecked = checked;
			}
			this.calcTotal(type);
		},
		//数量
		numberChange(data){
			this.cartList[data.index].number = data.number;
			this.calcTotal();
		},
		//删除
		deleteCartItem(index){
			let list = this.cartList;
			let row = list[index];
			let id = row.id;

			this.cartList.splice(index, 1);
			this.calcTotal();
			uni.hideLoading();
		},
		//清空
		clearCart(){
			uni.showModal({
				content: '清空购物车？',
				success: (e)=>{
					if(e.confirm){
						this.cartList = [];
					}
				}
			})
		},
		//计算总价
		calcTotal(){
			let list = this.cartList;
			if(list.length === 0){
				this.empty = true;
				return;
			}
			let total = 0;
			let checked = true;
			list.forEach(item=>{
				if(item.checked === true){
					total += item.price * item.number;
				}else if(checked === true){
					checked = false;
				}
			})
			this.allChecked = checked;
			this.total = Number(total.toFixed(2));
		},
		//创建订单
		createOrder(){
			let list = this.cartList;
			let goodsData = [];
			list.forEach(item=>{
				if(item.checked){
					let goodss = {
						"shop":"阿咩良品",
						"id" :item.id,
						"name":item.title,
						"price":item.price,
						"img":item.image,
						"specSelected":item.attr_val,
						"number":item.number
					};
					goodsData.push(goodss);
				}
			})
			if(goodsData==[])
			{
				uni.showToast({
					title:"请选择商品哦~"
				})
			}else{
				console.log(goodsData);
				uni.setStorage({
				    key: 'productGoods',
				    data: goodsData,
				    success:() => {
				        uni.navigateTo({
				        	url: '../order/createOrder'
				        })
				    }
				});
			}
		}
